<template>
    <div class="wrapper">
        <embed
                v-for="(item , i) in tabItems"
                :src="item.src"
                :key="i"
                type="weex"
                :style="{ visibility: item.visibility}"
                class="content"
                v-if="!is_web"
        ></embed>
        <div class="tabbar" append="tree">
            <tabitem
                    v-for="item in tabItems"
                    :key="item.index"
                    :index="item.index"
                    :icon="item.icon"
                    :title="item.title"
                    :titleColor="item.titleColor"
                    @tabItemOnClick="tabItemOnClick"
            ></tabItem>
        </div>
        <div v-if="is_web" style="background-color: #f6f6f6">
            <order v-if="selectedIndex == 0"></order>
            <check-order v-if="selectedIndex == 1"></check-order>
            <shop v-if="selectedIndex == 2"></shop>
            <user v-if="selectedIndex == 3"></user>
        </div>
    </div>
</template>

<style scoped >
    .wrapper {
        width: 750;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #f6f6f6;
    }
    .content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin-bottom: 88;
    }
    .tabbar {
        flex-direction: row;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 88;
    }
</style>

<script>
    import order from '../views/tabItems/order.vue'
    import checkOrder from '../views/tabItems/checkOrder.vue'
    import shop from '../views/tabItems/shop.vue'
    import user from '../views/tabItems/user.vue'

    export default {
        props: {
            tabItems: { default: [] },
            selectedColor: { default: '#ff0000' },
            unselectedColor: { default: '#000000' }
        },
        data: function () {
            return {
                selectedIndex: 0,
                is_web: false,
                is_ios: 0,
            }
        },
        components: {
            tabitem: require('./tabitem.vue'),
            order,
            checkOrder,
            shop,
            user,
        },
        created: function () {
            let platform = weex.config.env ? weex.config.env.platform : weex.config.platform;
            if (platform.toLowerCase() == 'web'){
                this.is_web = true;
            }
            this.select(this.selectedIndex);
        },
        methods: {
            tabItemOnClick: function (e) {
                this.selectedIndex = e.index;
                this.select(e.index);
                this.$emit('tabBarOnClick', e);
            },
            select: function(index) {
                for(var i = 0; i < this.tabItems.length; i++) {
                    var tabItem = this.tabItems[i];
                    if(i == index){
                        tabItem.icon = tabItem.selectedImage;
                        tabItem.titleColor = this.selectedColor;
                        tabItem.visibility = 'visible';
                    }
                    else {
                        tabItem.icon = tabItem.image;
                        tabItem.titleColor = this.unselectedColor;
                        tabItem.visibility = 'hidden';
                    }
                }
            },
        }
    }
</script>


